.warpper {
  padding: 10px; }

.github-corner svg {
  fill: #E1E1E1;
  color: #000;
  position: absolute;
  top: 0;
  border: 0;
  right: 0;
  z-index: 99;
  width: 70px;
  height: 70px; }

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out; }

@keyframes octocat-wave {
  0%, 100% {
    transform: rotate(0); }
  20%, 60% {
    transform: rotate(-25deg); }
  40%, 80% {
    transform: rotate(10deg); } }

.header {
  padding: 25px 22px 10px;
  font-size: 24px;
  color: #c1c1c1;
  font-size: 18px;
  font-weight: 700;
  display: flex; }
  .header .title {
    flex: 1; }
  .header .search {
    outline: 0;
    border: 0;
    background-color: transparent;
    border-bottom: 1px solid #afafaf;
    margin-right: 10px;
    padding-bottom: 3px;
    max-width: 80px;
    text-align: center; }
    .header .search::placeholder {
      color: #afafaf;
      text-align: center; }
  .header .tag {
    font-size: 12px;
    line-height: 25px;
    font-weight: normal;
    color: #868686;
    margin: 0 0 0 10px;
    z-index: 999;
    position: relative; }
    .header .tag span {
      display: inline-block;
      padding: 0 3px 0 3px;
      border-radius: 2px;
      height: 18px;
      line-height: 18px;
      margin: 0 2px;
      cursor: pointer;
      transition: background-color .3s; }
      .header .tag span:hover, .header .tag span:global(.active) {
        background-color: #868686;
        color: #fff; }

.noFind {
  min-height: 64px;
  align-items: center;
  justify-content: center;
  display: flex;
  color: #969696; }

.lists {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 10px; }
  .lists li {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin: 0 5px 10px 5px;
    min-width: 160px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.07);
    transition: all .3s; }
    .lists li:hover {
      box-shadow: 0 0px 5px rgba(0, 0, 0, 0.6); }
      .lists li:hover .star:not(:global(.active)) svg {
        animation-duration: 1s;
        animation-fill-mode: both;
        animation: twinkling .6s infinite ease-in-out; }
    .lists li a {
      color: #333;
      text-decoration: none; }
      .lists li a:hover {
        color: #333; }

@keyframes twinkling {
  0% {
    fill: #D64B24; }
  50% {
    fill: #e0e0e0; }
  100% {
    fill: #D64B24; } }

.itemHeader {
  flex: 1; }

.logo {
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  align-items: center;
  padding: 5px; }
  .logo h4 {
    padding: 0 10px 0 5px;
    margin: 0;
    line-height: 29px;
    vertical-align: middle;
    flex: 1; }
  .logo img {
    height: 23px;
    min-height: 23px;
    vertical-align: middle;
    border-radius: 3px;
    padding: 3px; }

.details {
  padding: 0px 10px 5px 10px;
  max-width: 190px;
  color: #969696; }

.bottomBar {
  padding: 0 10px;
  margin: 0 0 5px 0;
  display: flex; }
  .bottomBar svg {
    fill: #dcdbdb;
    fill: #e0e0e0;
    transition: all .3s;
    margin-right: 3px; }
    .bottomBar svg:hover {
      fill: #333; }
  .bottomBar img, .bottomBar svg {
    width: 12px;
    height: 12px; }
  .bottomBar .urls {
    flex: 1; }
  .bottomBar .star {
    display: flex;
    align-items: center; }
    .bottomBar .star:global(.active) svg {
      fill: #D64B24; }
      .bottomBar .star:global(.active) svg:hover {
        fill: #D64B24; }
    .bottomBar .star svg {
      cursor: pointer; }
      .bottomBar .star svg:hover {
        fill: #fbb601; }
  .bottomBar .zhhans:hover {
    fill: #D64B24; }
    .bottomBar .zhhans:hover circle {
      fill: #D64B24; }
    .bottomBar .zhhans:hover path {
      fill: #FBDC00; }
